import Comment from "../../../../../models/Comment";
import dbConnect from "../../../../../lib/dbConnect";
import { NextResponse } from "next/server";
import mongoose from "mongoose";
import jwt from "jsonwebtoken";

export async function POST(req, context) {
  await dbConnect();

  try {
    // --- Fix params ---
    const { commentId } = await context.params;

    // --- Fix token extraction ---
    const authHeader = req.headers.get("authorization") || "";
    if (!authHeader.startsWith("Bearer ")) {
      return NextResponse.json({ error: "Unauthorized" }, { status: 401 });
    }

    const token = authHeader.split(" ")[1];
    let decoded;
    try {
      decoded = jwt.verify(token, process.env.JWT_SECRET);
    } catch {
      return NextResponse.json({ error: "Invalid Token" }, { status: 401 });
    }

    const userId = decoded.id;

    const { text = "", emojis = [] } = await req.json();

    // --- Find the parent comment directly ---
    let parent = await Comment.findById(commentId);

    if (!parent) {
      return NextResponse.json(
        { error: "Comment not found" },
        { status: 404 }
      );
    }

    // --- Create reply subdocument ---
    const newReply = {
      _id: new mongoose.Types.ObjectId(),
      text,
      author: userId,
      emojis,
      likes: [],
      replies: [],
      createdAt: new Date(),
      updatedAt: new Date(),
    };

    // --- Push into replies ---
    parent.replies.push(newReply);
    await parent.save();

    // --- Populate reply author for frontend ---
    const populatedReply = await Comment.findOne(
      { "replies._id": newReply._id },
      { replies: 1 }
    )
      .populate("replies.author", "name avatarUrl")
      .lean();

    const returnedReply = populatedReply.replies.find(
      (r) => r._id.toString() === newReply._id.toString()
    );

    return NextResponse.json({
      success: true,
      comment: returnedReply,
    });
  } catch (err) {
    console.error("Reply Error:", err);
    return NextResponse.json({ error: "Server Error" }, { status: 500 });
  }
}
